	module.exports = {"pathContext":{"group":[{"node":{"id":"6452d6e3-c0c7-5599-b690-baf1aa74c652","slug":"lower-salt-intake-likely-to-have-had-key-role-in-plummeting-cardiovascular-disease-deaths-in-past-decade","title":"Lower salt intake likely to have had key role in plummeting cardiovascular disease deaths in past decade","excerpt":"<p>The 15% fall in dietary salt intake over the past decade in England is likely to have had a key role in the 40% drop in deaths from heart disease and stroke over the same period, concludes research published in BMJ Open. But average intake across the nation is still far too high, warn the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/04/14/lower-salt-intake-likely-to-have-had-key-role-in-plummeting-cardiovascular-disease-deaths-in-past-decade/\">Read More&#8230;</a></p>\n","status":"publish","date":"April 14, 2014","template":"","format":"standard"}},{"node":{"id":"b6b5adce-10db-57f6-8dc6-203634828dd6","slug":"the-perception-that-eating-disorders-are-a-womens-problem-delays-men-getting-help","title":"The perception that eating disorders are a women’s problem delays men getting help","excerpt":"<p>The widespread perception that only women have eating disorders is preventing men with these problems from getting the help and support they need, indicates a small study published in the online journal BMJ Open. Estimates suggest that around 1 in 250 women and 1 in 2000 men in the UK have anorexia nervosa, one of [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/04/08/the-perception-that-eating-disorders-are-a-womens-problem-delays-men-getting-help/\">Read More&#8230;</a></p>\n","status":"publish","date":"April 08, 2014","template":"","format":"standard"}},{"node":{"id":"af6635c1-6710-504c-b140-bf5dd31863fd","slug":"painkillers-linked-to-heightened-irregular-heartbeat-risk-in-older-adults","title":"Painkillers linked to heightened irregular heartbeat risk in older adults","excerpt":"<p>Current and recent use of painkillers/anti-inflammatories may be linked to a heightened risk of an irregular heartbeat (atrial fibrillation) among older adults, finds a large population study published in BMJ Open. Atrial fibrillation has itself been linked to stroke, heart failure, and reduced life expectancy, while previously published research has linked the use of non-steroidal [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/04/08/painkillers-linked-to-heightened-irregular-heartbeat-risk-in-older-adults/\">Read More&#8230;</a></p>\n","status":"publish","date":"April 08, 2014","template":"","format":"standard"}},{"node":{"id":"50d97a26-2652-5425-ac4c-f4a21598fca0","slug":"deadlines-dementia-and-radiation-most-read-articles-in-march","title":"Deadlines, dementia, and radiation: Most read articles in March","excerpt":"<p>March’s most read article was Gómez-Perretta et al.’s study on symptoms related to GSM radiation from mobile phone base stations. Newly-published papers in the top ten most read include Aziz Dhedhi et al.’s narrative analysis of GP’s accounts on the meaning of ‘timely’ diagnosis of dementia and also Herbert et al.’s paper on Australian researchers and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/04/04/deadlines-dementia-and-radiation-most-read-articles-in-march/\">Read More&#8230;</a></p>\n","status":"publish","date":"April 04, 2014","template":"","format":"standard"}},{"node":{"id":"da4af049-288d-552b-b8d5-6bc2c128c3b1","slug":"social-media-autism-and-diet-patterns-most-read-articles-in-february","title":"Social media, autism and diet patterns: Most read articles in February","excerpt":"<p>The most-read article in February was Brurberg et al.&#8217;s systematic review of case definitions for chronic fatigue syndrome. Hsiao et al.’s paper on road traffic injury mortality has also been popular. Other newly-published papers in the top ten most read include Jenkins et al.&#8217;s randomised controlled trial on the effect of a vegan low-carbohydrate diet on cardiovascular risk factors and body weight [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/03/24/social-media-autism-and-diet-patterns-most-read-articles-in-february/\">Read More&#8230;</a></p>\n","status":"publish","date":"March 24, 2014","template":"","format":"standard"}},{"node":{"id":"b1b0afcc-934e-5af8-872d-248c0fc53022","slug":"girls-born-small-or-underweight-twice-as-likely-to-be-infertile-in-adulthood","title":"Girls born small or underweight twice as likely to be infertile in adulthood","excerpt":"<p>Girls born unexpectedly small or underweight seem to be twice as likely to have fertility problems in adulthood as those of normal size at birth, suggests research published in BMJ Open. Medical advances mean that more underweight and very small babies will survive into adulthood, which might therefore increase the prevalence of fertility problems, say [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/03/10/girls-born-small-or-underweight-twice-as-likely-to-be-infertile-in-adulthood/\">Read More&#8230;</a></p>\n","status":"publish","date":"March 10, 2014","template":"","format":"standard"}},{"node":{"id":"9a25a7fe-17b0-5d05-87a2-d70345b6249e","slug":"bmj-opens-2013-year-in-review","title":"BMJ Open&#8217;s 2013 year in review","excerpt":"<p>&nbsp; 2013 was another successful year for BMJ Open. Credit and thanks as always goes to our editorial board and especially our peer reviewers for helping make this happen. BMJ Open’s status as a global journal was confirmed as we received over 2000 submissions from 89 countries and published papers from 60. We published 962 [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/02/17/bmj-opens-2013-year-in-review/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 17, 2014","template":"","format":"standard"}},{"node":{"id":"0093bd75-4beb-56eb-909d-61a70e7177e6","slug":"tobacco-industry-claims-plain-packs-wont-work-based-on-weak-evidence","title":"Tobacco industry claims “plain” packs won’t work based on weak evidence","excerpt":"<p>Most studies lack policy relevance; and relevant research lacks key indicators of quality, including peer review A critical evaluation of the volume, relevance and quality of evidence submitted by the tobacco industry to oppose standardised packaging of tobacco products doi:10.1136/bmjopen-2013-003757 Tobacco companies lack strong, relevant evidence to support their claims that standardised (plain) packaging of [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/02/12/tobacco-industry-claims-plain-packs-wont-work-based-on-weak-evidence/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 12, 2014","template":"","format":"standard"}},{"node":{"id":"796aab68-86c6-57e1-a715-50208d1952d8","slug":"global-salt-intake-smoking-cessation-through-facebook-and-the-cost-of-childbirth-most-read-articles-in-january","title":"Global salt intake, smoking cessation through Facebook and the cost of childbirth: Most read articles in January","excerpt":"<p>The most-read article in January was Snow et al.&#8217;s patient led study ‘What happens when patients know more than their doctors?’ on the impact of patient education on the lives of people with diabetes. Hsia et al.’s newly-published cross-sectional study on the variation in charges and prices paid for vaginal and caesarean births has also been popular. Other [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/02/06/global-salt-intake-smoking-cessation-through-facebook-and-the-cost-of-childbirth-most-read-articles-in-january/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 06, 2014","template":"","format":"standard"}},{"node":{"id":"762ac038-558d-5978-9691-993caf5b463c","slug":"thank-you-to-our-reviewers-2013","title":"Thank you to our reviewers &#8211; 2013","excerpt":"<p>Peer review is a fundamental part of publishing. Perhaps nowhere more so than in the open access field, which is often more scrutinised than other traditional publishing routes. Recognising this, the BMJ Open team would like to thank all 2725 peer reviewers who refereed for the journal in 2013 &#8211; your advice and considered remarks [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/01/16/thank-you-to-our-reviewers-2013/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 16, 2014","template":"","format":"standard"}}],"pathPrefix":"posts","first":false,"last":false,"index":17,"pageCount":26,"additionalContext":{}}}

